:root {
  --first-color: rgb(122,206,194);
  --second-color: rgb(230,223,204);
  --third-color: rgb(82, 84, 100);
  --fourth-color: rgb(233, 234, 241);
  --fifth-color: rgb(245,139,87);
  --text-color: rgb(255, 255, 255);

}
.sentMsg{
  padding-top: 18%;
}
.card-group{
 margin-top: 2rem;
}

.row{
  height: 100%;
}
.nav-pills .nav-link{
  color: rgb(46, 45, 45);
}
.btn-primary{
  background: var(--third-color);
  border: none;
}
.btn-secondary{
  background: var(--third-color);
  border: none;
}
.btn-primary:hover{
  background: var(--second-color);
  box-shadow: 2px 2px rgba(83, 81, 81, 0.1);
  transform: scale(103%, 103%);
  border: 1px solid rgb(100, 100, 97);
  z-index: 103;
}
.btn-secondary:hover{
  background: var(--second-color);
  box-shadow: 2px 2px rgba(83, 81, 81, 0.1);
  transform: scale(103%, 103%);
}
.nav-pills .nav-link.active, .nav-pills .show>.nav-link{
  background: var(--third-color)
}
a:hover{
  color: inherit;
}
.navbar{
  font-family: 'Comfortaa';
}
.glassCell{
  padding-top: 10px;
  padding-bottom: 10px;
  background: rgba(255, 255, 255, 0.37);
border-radius: 16px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(5.3px);
-webkit-backdrop-filter: blur(5.3px);
border: 1px solid rgba(255, 255, 255, 0.11);
}
.cardHeader{
  background: var(--third-color);
  color: var(--text-color);
  
}
.card{
  transition:  transform .3s;
}
.card:hover{
  transform: scale(105%, 105%);
  z-index: 101;

}
.tabsMargin{
  margin-bottom: 2rem;
  margin-top: 1rem;
}

.cardBody{
  background: transparent;
}
.cardFooter{
  background: transparent;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
}
.navbar{
  background: var(--third-color);
  
  
}

.navButton{
  border-radius: 3px;
  border: none;
  background: none;
  box-shadow: 6px blue;
  color:  var(--text-color);
}
.nameplate{
  color:  var(--text-color);
  text-align: center;
}
.navButton:hover{

  box-shadow: 2px 2px rgb(85, 68, 68), -1px 0 .4em rgb(146, 179, 241);
}
.formContainer{
  background: rgba(226, 95, 95, 0.199);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.3px);
  -webkit-backdrop-filter: blur(5.3px);
  border: 1px solid rgba(255, 255, 255, 0.19);
  border-radius: 3px;
  margin-top: 2rem;
  padding: 2rem;
  box-shadow: 5px 5px 5px rgb(235, 176, 166);
}
.tabContainer{
  box-shadow: 5px 5px 5px rgb(166, 188, 235);
}

.active{
  background: rgba(193, 195, 197, 0.397);
 }
 html, body {
  width: 100%;
  height:100%;
}

body {
  padding-top: 5%;
    background: linear-gradient(-45deg, #b1aaa7, #a2aaac, #b1babd, #bdb3b3);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
  
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}